var blueUtils = {
    //是否为数组
    isArray: function (val) {
        return val instanceof Array;
    },
    //是否为undefine 或者 null
    isUndef: function (val) {
        return val === undefined || val === null;
    },
    //是否为function
    isFunction: function (fn) {
        return typeof fn === 'function';
    },
    //执行function
    hook: function (context, cb, args) {
        if (args === void 0) { args = []; }
        if (this.isFunction(cb)) {
            return cb.apply(context, args);
        }
        return cb;
    },
    //遍历
    each: function (obj, cb, isReturn) {
        if (isReturn === void 0) { isReturn = false; }
        if (this.isUndef(obj))
            return;
        var i = 0, index = 0, newVal = [];
        var len = obj.length;
        if (this.isArray(obj)) {
            for (; i < len; i++) {
                if (isReturn) {
                    newVal.push(cb(obj[i], i));
                }
                else {
                    cb(obj[i], i);
                }
            }
        }
        else {
            for (i in obj) {
                if (!obj.hasOwnProperty(i))
                    continue;
                if (isReturn) {
                    newVal.push(cb(obj[i], i, index++));
                }
                else {
                    cb(obj[i], i, index++);
                }
            }
        }
        if (isReturn)
            return newVal;
    }
};
export default blueUtils;
